import { useState, useMemo } from "react";

import Chart from 'chart.js/auto';
import { Line } from 'react-chartjs-2';


import InputField from "./components/InputField";

// State
const App = () => {

    const [ principal, setPrincipal ]               = useState(100000);
    const [ rate, setRate ]                         = useState(10);
    const [ number, setNumber ]                     = useState(12);
    const [ time, setTime ]                         = useState(20);

    // 
    const futureValueList = useMemo(() => {
        const adjustedRate          = rate / 100 
        const tempFutureValueList   = [];

        // 
        for (let year=1; year<=time ; year++ ){

            // FV = P  [(1 + r/n)^(nt) - 1] / (r/n)

            const fv        = (principal * ( ( 1 + adjustedRate/number) ** ( number*year) -1) / (adjustedRate/number));
            const pr        = principal*year*number;
            const ga        = (fv-principal*year*number);

            tempFutureValueList.push({ fv, pr, ga });
        }
        return tempFutureValueList;

    }, [principal, rate, number, time]);

    // TIPS: setTime 
    // TIPS: onChangeevent(currying)(+)
    const handleChange      =  (setter) => (event) => {

        // 
        const value = Number(event.currentTarget.value) 

        const max   = Number(event.currentTarget.max)
        const min   = Number(event.currentTarget.min)

        if ( value > max || min > value){
            console.log("");
            return false;
        }


        setter(value); 
    }


    // TODO: 
    const renderGraph       = () => {
        // chart.js datasets label
        const fvObj = {};

        for (let fv of futureValueList) {
            for (let [key,value] of Object.entries(fv) ){
                if (!fvObj[key]) {
                    fvObj[key] = []; 
                }       
                fvObj[key].push(value);
            }
        }
        const data = {};

        if (!fvObj["fv"] || fvObj["fv"].length <= 0) {
            return false;
        }

        data.labels = fvObj["fv"].map((f, index) => `${index+1}`);

        data.datasets = [];
        data.datasets.push( {
            label: "",
            data : fvObj["fv"].map( (f) => parseFloat(f) ),
            fill : true,
        });
        data.datasets.push( {
            label: "",
            data : fvObj["pr"].map( (f) => parseFloat(f) ),
            fill : true,
        });

        return (
            <>
                <Line data={data} />
            </>
        );
    }

    // 2
    const fixed = (data) => {
        return humanize(data.toFixed(2))
    }

    // 3
    const humanize = (data) => {
        const parts = String(data).split(".");
        parts[0]    = Number(parts[0]).toLocaleString()

        return parts.join(".")
    }


    return (
        <>
            <h1 className="header"></h1>

            <main className="main_area">
                
                <h2></h2>

                <InputField label="(%)" value={rate.toString()} onChange={handleChange(setRate)} min="0" max="100" placeholder="" />
                <InputField label="()" value={principal.toString()} onChange={handleChange(setPrincipal)} min="0" max="100000000" placeholder="()" />
                <InputField label="()" value={number.toString()} onChange={handleChange(setNumber)} min="1" max="1000" placeholder="()" />
                <InputField label="()" value={time.toString()} onChange={handleChange(setTime)} min="1" max="100" placeholder="()" />

                <p className="annotate">()12365</p>

                <hr />

                <h2></h2>
                
                <div>{rate}%{principal}1{number}{time}</div>

                { (() => {
                    if (futureValueList.length === 0){
                        return <div></div>;
                    }
                    const lastFutureValue   = futureValueList[futureValueList.length-1];
                    return (
                        <>
                            <div>: <span className="result">{ fixed(lastFutureValue.fv) } </span></div>
                            <div>: <span className="result">{ humanize(lastFutureValue.pr) } </span> : <span className="result">{ fixed(lastFutureValue.ga) } </span></div>
                        </>
                    )
                }) () }


                <hr />

                <h2></h2>

                {renderGraph()}

                
                <hr />

                <h2></h2>
                <table>
                    <thead>
                        <tr>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                        </tr>
                    </thead>
                    <tbody>
                        { 
                            futureValueList.map( (fv, index) => (
                                <tr key={index}>
                                    <td className="text-center">{index+1}</td>
                                    <td className="text-end">{humanize(fv.pr)}</td>
                                    <td className="text-end">{fixed(fv.ga)}</td>
                                    <td className="text-end">{fixed(fv.fv)}</td>
                                </tr>
                            )
                        )}
                    </tbody>
                </table>

            </main>
        </>
    );
}

export default App
